.card-gif {
  box-sizing: border-box;
  background: #274684;
  -webkit-animation: showBlock 0.7s ease-out forwards;
          animation: showBlock 0.7s ease-out forwards;
  -webkit-transition: 1s ease-out;
  transition: 1s ease-out; }

.img-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2px; }

@-webkit-keyframes showBlock {
  0% {
    width: 0;
    height: 0; }
  100% {
    width: 100%;
    height: 100%; } }

@keyframes showBlock {
  0% {
    width: 0;
    height: 0; }
  100% {
    width: 100%;
    height: 100%; } }

.modal {
  display: flex;
  justify-content: center;
  align-items: center; }

.no-more-gifs-text {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 4vh;
  margin: auto;
  color: #274684; }

.load-bar {
  position: fixed;
  top: 64px;
  width: 100%;
  height: 6px;
  background-color: #fdba2c;
  z-index: 1011; }

.bar {
  content: "";
  display: inline;
  position: absolute;
  width: 0;
  height: 100%;
  left: 50%;
  text-align: center; }

.bar:nth-child(1) {
  background-color: #da4733;
  -webkit-animation: loading 3s linear infinite;
          animation: loading 3s linear infinite; }

.bar:nth-child(2) {
  background-color: #3b78e7;
  -webkit-animation: loading 3s linear 1s infinite;
          animation: loading 3s linear 1s infinite; }

.bar:nth-child(3) {
  background-color: #fdba2c;
  -webkit-animation: loading 3s linear 2s infinite;
          animation: loading 3s linear 2s infinite; }

@-webkit-keyframes loading {
  from {
    left: 50%;
    width: 0;
    z-index: 100; }
  33.3333% {
    left: 0;
    width: 100%;
    z-index: 10; }
  to {
    left: 0;
    width: 100%; } }

@keyframes loading {
  from {
    left: 50%;
    width: 0;
    z-index: 100; }
  33.3333% {
    left: 0;
    width: 100%;
    z-index: 10; }
  to {
    left: 0;
    width: 100%; } }

@media screen and (max-width: 600px) {
  .load-bar {
    top: 55px; } }

.gif-container {
  width: 1450px;
  overflow-y: auto;
  position: relative;
  margin-top: 10px; }
  .gif-container::-webkit-scrollbar {
    width: 0;
    height: 0; }
  .gif-container li {
    list-style-type: none;
    -webkit-transition: opacity 0.4s;
    transition: opacity 0.4s; }
  .gif-container img {
    -webkit-transition: opacity 0.4s;
    transition: opacity 0.4s; }

@media screen and (max-width: 1440px) {
  .gif-container {
    width: 1224px; } }

@media screen and (max-width: 1236px) {
  .gif-container {
    width: 1020px; } }

@media screen and (max-width: 1032px) {
  .gif-container {
    width: 816px; } }

@media screen and (max-width: 828px) {
  .gif-container {
    width: 612px; } }

@media screen and (max-width: 624px) {
  .gif-container {
    width: 408px; } }

@media screen and (max-width: 420px) {
  .gif-container {
    width: 204px; } }

